import { CommonService } from './../../../../shared/common.service';
import { PrinterService } from './../../../../shared/printer.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DialogService } from 'ng2-bootstrap-modal';
import { ToastrService } from 'ngx-toastr';
import { PermisotaxisBitacoraResponseInterface } from './permisotaxis-response.interface';
import { Component, OnInit } from '@angular/core';
import { PermisotaxisBitacoraService } from './permisotaxis.service';
import * as _ from 'lodash';

@Component({
selector: 'permisotaxis-bitacora',
templateUrl: './permisotaxis-bitacora.html',
styleUrls: ['./permisotaxis-bitacora.scss'],
})
export class PermisotaxisBitacoraComponent implements OnInit {
    data;
    filterQuery = '';
    rowsOnPage = 10;
    numero: string;
    sortBy = 'idpermisotaxi';
    sortOrder = 'asc';
    backpage: boolean;

    idpermisotaxi: number;

    constructor(
      private service: PermisotaxisBitacoraService, 
      private toastrService: ToastrService, 
      private dialogService: DialogService, 
      private printerService: PrinterService,
      private commonService: CommonService,
      private route: ActivatedRoute, 
      private router: Router) {
    }
    ngOnInit() {
      this.route.params.subscribe(params => {
        if (params['idpermisotaxi'] !== undefined) {
          const idpermisotaxi = +params['idpermisotaxi'];
          this.findByIdPermisoTaxi(idpermisotaxi);
          this.idpermisotaxi = idpermisotaxi;
          this.backpage = true;
        }

      });
    }
    backPage() {
        window.history.back();
    }
    private findByIdPermisoTaxi(id: number): void {
      this.service
        .findByIdPermisoTaxi(id)
        .subscribe(
            (data: PermisotaxisBitacoraResponseInterface) => {
                if (data.success) {
                  if (data.result[0]) {

                    // OBTIENE NMERO DE PERMISO DE PRIMER REGISTRO ENCONTRADO
                    if (data.result[0].numero) {
                      this.numero = data.result[0].numero;
                    }

                    this.data = data.result;

                  }
                } else {
                this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }

    filtrarFechas(fechaDesde, fechaHasta) {
      this.service
        .allFromTo(fechaDesde, fechaHasta, this.idpermisotaxi)
        .subscribe(
            (data: PermisotaxisBitacoraResponseInterface) => {
                if (data.success) {
                  const result = _.chain(data.result)
                    .sortBy('liquidaciones.fecha')
                    .value();

                  this.data = result;
                } else {
                  this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    
    descargarCSV(json, reportTitle, showLabel) {
      this.commonService.JSONToCSVConvertor(json, reportTitle, showLabel);
    }

    printReporte(permisos: any) {
      this.printerService.printBitacoraPermiso(permisos, this.numero);
    }
  }



