import { PrinterService } from './../../../../shared/printer.service';
import { CommonService } from './../../../../shared/common.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DialogService } from 'ng2-bootstrap-modal';
import { ToastrService } from 'ngx-toastr';
import { PermisotaxisReporteResponseInterface } from './permisotaxis-reporte-response.interface';
import { Component, OnInit } from '@angular/core';
import { PermisotaxisReporteService } from './permisotaxis-reporte.service';
import * as _ from 'lodash';

@Component({
selector: 'permisotaxis-reporte',
templateUrl: './permisotaxis-reporte.html',
styleUrls: ['./permisotaxis-reporte.scss'],
})
export class PermisotaxisReporteComponent implements OnInit {
    data;
    filterQuery = '';
    rowsOnPage = 10;
    sortBy = 'idpermisotaxi';
    sortOrder = 'asc';
    backpage: boolean;
    
    liquidaciones;
    ordenes;
    total_ingreso;
    total_egreso;
    vehiculo;
    
    idpermisotaxi: number;
    permiso: string;

    constructor(
      private service: PermisotaxisReporteService, 
      private toastrService: ToastrService, 
      private dialogService: DialogService, 
      private commonService: CommonService,
      private printerService: PrinterService,
      private route: ActivatedRoute, 
      private router: Router) {
    }
    ngOnInit() {
      this.route.params.subscribe(params => {
        if (params['idpermisotaxi'] !== undefined) {
          const idpermisotaxi = +params['idpermisotaxi'];
          this.findByIdPermisoTaxi(idpermisotaxi);
          this.idpermisotaxi = idpermisotaxi;
          this.backpage = true;
        }

      });
    }
    backPage() {
        window.history.back();
    }
    private findByIdPermisoTaxi(id: number): void {
      this.service
        .findByIdPermisoTaxi(id)
        .subscribe(
            (data: PermisotaxisReporteResponseInterface) => {
                if (data.success) {
                  this.data = data.result;
                  this.permiso = data.result.vehiculo[0].numero;
                  this.liquidaciones = data.result.liquidaciones;
                  this.ordenes = data.result.ordenes;
                  this.total_ingreso = data.result.total_ingreso;
                  this.total_egreso = data.result.total_egreso;
                  this.vehiculo = data.result.vehiculo;
                } else {
                  this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }

    filtrarFechas(fechaDesde, fechaHasta) {
      this.service
        .allFromTo(fechaDesde, fechaHasta, this.idpermisotaxi)
        .subscribe(
            (data: PermisotaxisReporteResponseInterface) => {
                if (data.success) {
                  const result = data.result;

                  this.data = data.result;
                  this.permiso = data.result.vehiculo[0].numero;
                  this.liquidaciones = data.result.liquidaciones;
                  this.ordenes = data.result.ordenes;
                  this.total_ingreso = data.result.total_ingreso;
                  this.total_egreso = data.result.total_egreso;
                  this.vehiculo = data.result.vehiculo;
                } else {
                  this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    
    descargarCSV(json, reportTitle, showLabel) {
      this.commonService.JSONToCSVConvertor(json, reportTitle, showLabel);
    }

    printReporte(permiso: any) {
      console.log('permiso', permiso);
      this.printerService.printReportePermiso(permiso);
    }
  }



