import { PrinterService } from './../../../../shared/printer.service';
import { CommonService } from './../../../../shared/common.service';
import { TallersBitacoraResponseInterface } from './taller-response.interface';
import { Router, ActivatedRoute } from '@angular/router';
import { DialogService } from 'ng2-bootstrap-modal';
import { ToastrService } from 'ngx-toastr';
import { Component, OnInit } from '@angular/core';
import { TallersBitacoraService } from './taller.service';
import * as _ from 'lodash';

@Component({
selector: 'taller-bitacora',
templateUrl: './taller-bitacora.html',
styleUrls: ['./taller-bitacora.scss'],
})
export class TallersBitacoraComponent implements OnInit {
    data;
    backpage: boolean;

    orden = [];
    refaccion = [];
    egresoconcepto = [];

    taller: string;
    total_orden_ingreso: number;
    total_egresoconcepto_egreso: number;
    total_refaccion_egreso: number;

    total_ingreso: number;
    total_egreso: number;
    ganancia: number;

    idtaller: number;


    constructor(
      private service: TallersBitacoraService, 
      private toastrService: ToastrService, 
      private dialogService: DialogService, 
      private route: ActivatedRoute, 
      private commonService: CommonService,
      private printerService: PrinterService,
      private router: Router) {
    }
    ngOnInit() {
      this.route.params.subscribe(params => {
        if (params['idtaller'] !== undefined) {
          const idtaller = +params['idtaller'];
          this.findByIdTaller(idtaller);
          this.idtaller = idtaller;
          this.backpage = true;
        }

      });
    }
    backPage() {
        window.history.back();
    }
    private findByIdTaller(id: number): void {
      this.service
        .findByIdTaller(id)
        .subscribe(
            (data: TallersBitacoraResponseInterface) => {
                if (data.success) {
                    this.data = data.result;
                    this.orden = data.result.orden;
                    this.refaccion = data.result.refaccion;
                    this.egresoconcepto = data.result.egresoconcepto;
                    this.taller = data.result.taller;
                    this.total_orden_ingreso = data.result.total_orden_ingreso;
                    this.total_egresoconcepto_egreso = data.result.total_egresoconcepto_egreso;
                    this.total_refaccion_egreso = data.result.total_refaccion_egreso;
                    this.total_ingreso = data.result.total_ingreso;
                    this.total_egreso = data.result.total_egreso;
                    this.ganancia = data.result.ganancia;
                } else {
                  this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    
    filtrarFechas(fechaDesde, fechaHasta) {
      this.service
        .allFromTo(fechaDesde, fechaHasta, this.idtaller)
        .subscribe(
            (data: TallersBitacoraResponseInterface) => {
                if (data.success) {
                  const result = _.chain(data.result)
                    .sortBy('orden.fecha')
                    .value();

                  this.data = data.result;
                  this.orden = data.result.orden;
                  this.refaccion = data.result.refaccion;
                  this.egresoconcepto = data.result.egresoconcepto;
                  this.taller = data.result.taller;
                  this.total_orden_ingreso = data.result.total_orden_ingreso;
                  this.total_egresoconcepto_egreso = data.result.total_egresoconcepto_egreso;
                  this.total_refaccion_egreso = data.result.total_refaccion_egreso;
                  this.total_ingreso = data.result.total_ingreso;
                  this.total_egreso = data.result.total_egreso;
                  this.ganancia = data.result.ganancia;
                } else {
                  this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    
    descargarCSV(json, reportTitle, showLabel) {
      this.commonService.JSONToCSVConvertor(json, reportTitle, showLabel);
    }

    printReporte(taller: any) {
      this.printerService.printBitacoraTaller(taller);
    }
  }



